import React, {useEffect, useState} from 'react'
import s from './Dialogs.module.css'
import {getMyDialogs, InitialStateType, sendMessageReq, startMessaging} from '../../Redux/dialogsReducer'
import {Message} from './Message'
import {DialogItem} from './DialogItem'
import {useDispatch, useSelector} from 'react-redux'
import {getDialogsSelect, getMessagesSelect} from '../../Redux/Selectors/dialogs-selectors'
import {getIsFetching} from '../../Redux/Selectors/users-selectors'
import {ArrowLeftOutlined, LoadingOutlined} from '@ant-design/icons'
import {MessagesType} from '../../types/types'
import {selectCurrentUserId} from '../../Redux/Selectors/auth-selectors'
import {useParams} from 'react-router-dom'
import {SendMessageForm} from './SendMessageForm'

export type PropsType = {
    dialogsPage: InitialStateType
    sendMessage: (messageText: string) => void
    meSender: boolean
}
export type NewMessageFormValuesType = {
    body: string
}

const Dialogs: React.FC<PropsType> = (props) => {
    const [messageMode, setMessageMode] = useState(false)
    const [actualId, setActualId] = useState(0)
    const dialogs = useSelector(getDialogsSelect)
    const messages = useSelector(getMessagesSelect)
    const isFetching = useSelector(getIsFetching)
    const currentId = useSelector(selectCurrentUserId)
    const dispatch = useDispatch()


    let userId: any
    ({userId} = useParams())

    useEffect(() => {
        if (userId) {
            startChat(userId)
            console.log('start messaging')
        }
        if (!userId) {
            setMessageMode(false)
        }
    }, [userId])

    const startChat = (id: number) => {
        setMessageMode(true)
        dispatch(startMessaging(id, 1, null))
        setActualId(id)

    }
    useEffect(() => {
        dispatch(getMyDialogs())
        setMessageMode(false)
    }, [])

    let dialogsElements = dialogs.map((dialogs) => (

        <DialogItem startChat={startChat} dialog={dialogs} key={dialogs.id}/>
    ))


    let messagesElements = messages.slice(0).reverse().map((m) => (
        // @ts-ignore
        <Message myId={currentId} message={m} key={m.id}/>
    ))

    let sendMessage = (id: number, body: MessagesType) => {
        dispatch(sendMessageReq(id, body))
    }

    return (
        <div className={s.dialogs}>
            {!messageMode ?
                <div>{dialogsElements}</div> :
                (<div className={s.dialogsHeight}>
                    <div className={s.backArrow} onClick={() => setMessageMode(false)}><ArrowLeftOutlined/></div>
                    <div className={s.messageContainer}>
                        {!isFetching ?
                            <div style={{
                                display: 'flex',
                                justifyContent: 'center',
                                alignItems: 'center',
                                height: '100%'
                            }}>
                                <LoadingOutlined
                                    style={{fontSize: 50, margin: 'auto', padding: 'auto'}}/></div> : messagesElements}
                    </div>
                    <SendMessageForm id={actualId} onSubmit={sendMessage}/>
                </div>)}

        </div>
    )
}

export default Dialogs
